import React, { useState, useEffect } from "react";

export default function Main() {
  const [typingText, setTypingText] = useState("Loading");
  const [timer, setTimer] = useState(0);

  const quoteDisplayElement = document.getElementById("quoteDisplay");
  const quoteInputElement = document.getElementById("quoteInput");
  const timerElement = document.getElementById("timer");
  const speedElement = document.getElementById("speed");
  const playButton = document.querySelector("button");

  const Typing = () => {
    const arrayQuote = quoteDisplayElement.querySelectorAll("span");
    const arrayValue = quoteInputElement.value.split("");

    let correct = true;
    arrayQuote.forEach((characterSpan, index) => {
      const character = arrayValue[index];
      if (character == null) {
        characterSpan.classList.remove("correct");
        characterSpan.classList.remove("incorrect");
        correct = false;
      } else if (character === characterSpan.innerText) {
        characterSpan.classList.add("correct");
        characterSpan.classList.remove("incorrect");
      } else {
        characterSpan.classList.remove("correct");
        characterSpan.classList.add("incorrect");
        correct = false;
      }
    });

    if (correct) {
      const spans = document.querySelectorAll(".correct");
      const word = spans.length / 4.7;
      const speed = Math.round((word / timerElement.innerHTML) * 60);
      if (speed === 0 || speed === NaN) {
        speedElement.innerHTML = "";
        alert("Please press play");
      } else {
        speedElement.innerHTML = `Your speed is ${speed} words per minute`;
        playButton.innerHTML = "Play Again";
      }
      timerElement.style.display = "none";
      playButton.disabled = false;
      setTimer((timer) => timer * 0);
      quoteDisplayElement.innerHTML = "";
      quoteInputElement.value = null;
    }
  };

  function getRandomQuote() {
    fetch("https://api.quotable.io/random")
      .then((response) => response.json())
      .then((data) =>
        setTypingText(() => {
          return data.content;
        })
      );
  }

  useEffect(() => {
    getRandomQuote();
  }, []);

  const playGame = () => {
    try {
      setTimer(0);
      timerElement.style.display = "block";
      speedElement.innerHTML = "";
      quoteInputElement.focus();
      typingText.split("").forEach((character) => {
        const characterSpan = document.createElement("span");
        characterSpan.innerText = character;
        quoteDisplayElement.appendChild(characterSpan);
      });
      quoteInputElement.value = null;
    } catch (err) {
      alert(err);
    }
    getRandomQuote();
    playButton.disabled = true;
  };

  useEffect(() => {
    const interval = setInterval(() => {
      setTimer((timer) => timer + 1);
    }, 1000);

    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <div className="main">
      <div className="speed-section">
        <h2 className="speed" id="speed"></h2>
        <div className="timer" id="timer">
          {timer}
        </div>
      </div>
      <div className="text-box quote-display" id="quoteDisplay"></div>
      <div className="type-box">
        <textarea
          id="quoteInput"
          className="quote-input"
          onChange={Typing}
          autoFocus
          autoComplete="off"
        ></textarea>

        <button onClick={playGame}> Play! </button>
      </div>
    </div>
  );
}
